.table {
  border-spacing: 0;
  border: 1px solid black;
  min-width: 50vw;
  margin: 0 auto;
}
.table__cell{
  margin: 0;
  padding: 0.5rem;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}
.table__head{
  text-transform: uppercase;
}
.table__name{
  font-weight: bolder;
}